import React, { Component } from 'react';
import QuestionA from '../QuestionA/QuestionA'
import QuestionB from '../QuestionB/QuestionB'
import QuestionC from '../QuestionC/QuestionC'
import QuestionD from '../QuestionD/QuestionD'
import QuestionE from '../QuestionE/QuestionE'
import Buttons from '../Buttons/Buttons'
import Result from '../Result/Result'

class Questions extends Component {
    state = { 
        isFinished: false,
        result: 0,
        rightAnswers: 0,
        totalScore: [0],
        answerA: 'харьков',
        inputValue: '',
        answerE: 'харьков',
        inputValueE: '',
        answerD: 'chernozem',
        answerC: 'dnepr',
        answerB: '1'
    }
 
    getValue = event => {
        let inp = event.target.value.toLowerCase();
        this.setState({
            inputValue: inp
        })              
    }

    checkAnswer = event => {
        let answer = this.state.answerA
        if (this.state.inputValue === answer) {
            let result = this.state.totalScore
            this.setState({
                result: result.push(1)
            })
           
        } 
    }

    getValueE = event => {
        let inp = event.target.value.toLowerCase();
        this.setState({
            inputValueE: inp
        })
       
    }

    checkAnswerE = event => {
        let answer = this.state.answerE
        if (this.state.inputValueE === answer) {
            let result = this.state.totalScore
            this.setState({
                result: result.push(1)
            })
           
        } 
    }

    checkRadio = event => { 
        let radio = event.target.id
        let answer = this.state.answerD
        if (radio === answer) {
            let result = this.state.totalScore
            this.setState({
                result: result.push(1)
            })
        }
        
    }

    checkCheckbox = event => {
        let checkbox = event.target.id
        let answer = this.state.answerC
        if (checkbox === answer) {
            let result = this.state.totalScore
            this.setState({
                result: result.push(1)
            })
        }
        
    }

    checkSelect = event => {
        let select = event.target.value
        let answer = this.state.answerB
        if (select === answer) {
            let result = this.state.totalScore
            this.setState({
                result: result.push(1)
            })
        }
        
    }

    calculateScore = event => {

        const checkAinput = document.getElementById('inputtext').value
        const checkEinput = document.getElementById('inputtextE').value
        const checkBselect = document.getElementById('selectB').value
        const checkCcheckbox = document.getElementById('dnepr')
        const checkDradio = document.getElementById('chernozem')

        let total = this.state.totalScore
        let totalScore = total.reduce(function(elem, sum){
            return elem+sum
        },0)

        if (checkAinput === '' || checkEinput === '' || 
        checkBselect === 'выбрав из предложенных вариантов' || 
        !checkCcheckbox.checked || !checkDradio.checked) {

            const conf = window.confirm(`Каждый не отвеченный ответ считается неправильным, Вы уверены что хотите продолжить?`)
            if (conf) {
                this.setState({
                    result: totalScore
                })
                this.setState({
                    isFinished: true
                })
                this.setState({
                    rightAnswers: totalScore
                })
            } else {
                this.setState({
                    isFinished: false
                })
            }

        } else {
            this.setState({
                result: totalScore
            })
            this.setState({
                isFinished: true
            })
            this.setState({
                rightAnswers: totalScore
            })
        }     
    }

    render() { 
        return ( 
            <div className="text-center">
            {
            this.state.isFinished
            ? <Result score={this.state.result} rightAnswers={this.state.rightAnswers}/> 
            : <React.Fragment>
            <QuestionA onCheckA={this.checkAnswer} onCheckB={this.getValue}/>
            <QuestionB onCheckB={this.checkSelect}/>
            <QuestionE onCheckE={this.checkAnswerE} onCheckEE={this.getValueE}/>
            <QuestionC onCheckC={this.checkCheckbox}/>
            <QuestionD onCheckD={this.checkRadio}/>
            <Buttons onCheckCalc={this.calculateScore}/>
            </React.Fragment>
            }
           
            </div>
        );
    }
}
 
export default Questions;